// vue

import { mapGetters, mapMutations, mapActions } from 'vuex'
import { playMode } from '_common/js/config'
import { shuffle } from '_common/js/util'

export const playlistMixin = { // scrollmixin
    computed: {
        ...mapGetters(['playList'])
    },
    mounted () {
        this.handlePlaylist(this.playList)
    },
    activated () {
        this.handlePlaylist(this.playList)
    },
    watch: {
        playList (newV) {
            this.handlePlaylist(this.playList)
        }
    },
    methods: {
        handlePlaylist (playList) {

        }
    }
}

// playerplaylisticonmixin
export const playerMixin = {
    methods: {
        changeMode () { // 
            const mode = (this.mode + 1) % 3 // 3
            this.setPlayMode(mode)
            let list = null
            if (mode == playMode.random) {
                list = shuffle(this.sequenceList) // 
            } else {
                list = this.sequenceList
            }
            this.resetCurrentSong(list) // 
            this.setPlayList(list) // vuex
        },
        resetCurrentSong (list) { // 
            let index = list.findIndex((item) => { // arr.findIndexes6index
                return item.id === this.currentSong.id
            })
            this.setCurrentIndex(index)
        },
        getFavoriteIcon (currentSong) { // clas
            return this.isFavorite(currentSong) ? 'icon-favorite' : 'icon-not-favorite'
        },
        toggleFavorite (currentSong) { // 
            if (this.isFavorite(currentSong)) {
                this.deleteFavoriteList(currentSong)
            } else {
                this.saveFavoriteList(currentSong)
            }
        },
        isFavorite (song) {
            const index = this.favoriteList.findIndex((item) => {
                return item.id === song.id
            })
            if (index < 0) {
                return false
            } else {
                return true
            }
        },
        ...mapMutations({
            setPlayState: 'SET_PLAYING_STATE',
            setCurrentIndex: 'SET_CURRENT_INDEX', // vuexcurrentindex
            setPlayMode: 'SET_PLAY_MODE', // 
            setPlayList: 'SET_PLAYLIST' // 
        }),
        ...mapActions(['saveFavoriteList', 'deleteFavoriteList'])
    },
    computed: {
        iconMode () { // 
            return this.mode === playMode.sequence ? 'icon-sequence' : this.mode === playMode.loop ? 'icon-loop' : 'icon-random'
        },
        ...mapGetters([
            'fullScreen',
            'playList',
            'currentSong',
            'currentIndex',
            'playing',
            'sequenceList',
            'mode',
            'favoriteList'
        ])
    }
}

// search  song-addmixin
export const searchMixin = {
    data () {
        return {
            query: ''
        }
    },
    computed: {

    },
    methods: {
        beforeScroll () { // input
            this.$refs.searchBox.blur() // input
        },
        addQuery (k) { // query
            this.$refs.searchBox.setQuery(k)
        },
        onQueryChange (newq) { // query
            this.query = newq
        },
        deleteOneSH (item) { // 
            this.deleteSearchHistory(item)
        },
        selectSearchHistory (item) { // 
            this.addQuery(item)
        },
        ...mapActions(['saveSearchHistory', 'deleteSearchHistory'])
    }
}



