/* storage */
import storage from 'good-storage'

const SEARCH_KEY = '__search__' // search
const SEARCH_KEY_LENGTH = 15 // search
const PLAY_KEY = '__play__' // play
const PLAY_KEY_LENGTH = 200 // play
const FAVORITE_KEY = '_favorite_' // favoriteListlocalstorage
const FAVORITE_MAX_LENGTH = 200 // favoriteList

// 1.arr
function insertArray (arr, val, compare, maxlen) {
    const index = arr.findIndex(compare)
    if (index === 0) {
        return true
    }
    if (index > 0) {
        arr.splice(index, 1)
    }
    arr.unshift(val) // 
    if (maxlen && arr.length > maxlen) {
        arr.pop() // 
    }
}

// 2.
export function saveSearch (query) {
    let searches = storage.get(SEARCH_KEY, [])
    insertArray(searches, query, (item) => { return item === query }, SEARCH_KEY_LENGTH)
    storage.set(SEARCH_KEY, searches) // storagestoragenpmgood-storage
    return searches
}

// 3.
export function loadSearch () {
    console.log(storage.get(SEARCH_KEY, []))
    return storage.get(SEARCH_KEY, [])
}

// 4.arr
function deleteArray (arr, compare) {
    const index = arr.findIndex(compare)
    if (index < 0) {
        return false
    }
    arr.splice(index, 1)
}

// 5.
export function deleteSearch (query) {
    let searches = storage.get(SEARCH_KEY, [])
    deleteArray(searches, (item) => { return item === query })
    storage.set(SEARCH_KEY, searches)
    return searches
}

// 6.localstoragevuex
export function clearSearch () {
    storage.remove(SEARCH_KEY)
    return []
}

// 7
export function savePlay (play) {
    let plays = storage.get(PLAY_KEY, [])
    insertArray(plays, play, (item) => { return item.id === play.id }, PLAY_KEY_LENGTH)
    storage.set(PLAY_KEY, plays) // storagestoragenpmgood-storage
    return plays
}

// 
export function loadPlay () {
    const plays = storage.get(PLAY_KEY, [])
    return plays
}

// 8.
export function deletePlay (play) {
    let plays = storage.get(PLAY_KEY, [])
    deleteArray(plays, (item) => { return item === play })
    storage.set(PLAY_KEY, plays)
    return plays
}

// 9.localstoragevuex
export function clearPlay () {
    storage.remove(PLAY_KEY)
    return []
}

// 10.favoriteList
export function saveFavorite (song) {
    var favorite = storage.get(FAVORITE_KEY, [])
    insertArray(favorite, song, (item) => {
        return song.id === item.id
    }, FAVORITE_MAX_LENGTH)
    storage.set(FAVORITE_KEY, favorite)
    return favorite
}

// 11.
export function deleteFavorite (song) {
    var favorite = storage.get(FAVORITE_KEY, [])
    deleteArray(favorite, (item) => {
        return song.id === item.id
    })
    storage.set(FAVORITE_KEY, favorite)
    return favorite
}

// 12.
export function loadFavorite () {
    var favorite = storage.get(FAVORITE_KEY, [])
    return favorite
}



